





























































import { Vue, Component, Prop, Watch } from 'vue-property-decorator';

import DeleteMaterialDialog from '../delete/DeleteMaterialDialog.vue';

@Component({
  components: {
    DeleteMaterialDialog
  }
})
export default class EditSandGravelMaterial extends Vue {
  @Prop({ required: true }) public readonly item!: _object;
  public dialogState = false;

  @Watch('item.material')
  onUpdateName() {
    this.item.type = this.item.material.toLowerCase().replaceAll(' ', '_');
  }

  public onDeleteItem() {
    this.$emit('deleteItem');
    this.dialogState = false;
  }
}
